<template>
  <div class="moduleBoxView">

    <div class="moduleFrom">
      <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="100" label-position="right">
        <el-form-item :label="$t('UserView.account')" :label-width="formLabelWidth">
          <el-input v-model="fromData.account" :placeholder="$t('UserView.accountPlaceholder')" clearable></el-input>
        </el-form-item>
        <el-form-item :label="$t('UserView.nameQuery')" :label-width="formLabelWidth">
          <el-input v-model="fromData.name" :placeholder="$t('UserView.nameQueryPlaceholder')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('UserView.sex')" :label-width="formLabelWidth">
          <el-select v-model="fromData.sex" :placeholder="$t('UserView.sexPlaceholder')">
            <el-option :value="0" :label="$t('pullDown.sex.secret')"/>
            <el-option :value="1" :label="$t('pullDown.sex.man')"/>
            <el-option :value="2" :label="$t('pullDown.sex.woman')"/>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('UserView.role')" :label-width="formLabelWidth">
          <el-select filterable v-model="fromData.role" :placeholder="$t('UserView.rolePlaceholder')">
            <el-option v-for="item in selectData.roleList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('UserView.phoneQuery')" :label-width="formLabelWidth">
          <el-input v-model="fromData.phone" :placeholder="$t('UserView.phoneQueryPlaceholder')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('UserView.weixinQuery')" :label-width="formLabelWidth">
          <el-input v-model="fromData.weixin" :placeholder="$t('UserView.weixinQueryPlaceholder')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('UserView.birthDate')" style="width: 25%" :label-width="formLabelWidth">
          <el-date-picker v-model="fromData.birthDateListList"
                          format="YYYY-MM-DD" value-format="YYYY-MM-DD" type="daterange"
                          :start-placeholder="$t('pullDown.daterange.start')"
                          unlink-panels :range-separator="$t('pullDown.daterange.range')"
                          :end-placeholder="$t('pullDown.daterange.end')"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="listData()" icon="Search">
            {{ $t('common.button.search') }}
          </el-button>
          <el-button type="info" @click="reset()" icon="Loading">
            {{ $t('common.button.reset') }}
          </el-button>
          <el-button type="success" v-show="isButton('user.add')" @click="add()">
            {{ $t('common.button.add') }}
          </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="moduletTable" ref="tableDiv">
      <el-table :data="tableData" :height="tabHeight" :empty-text="$t('common.table.noData')" stripe border fit highlight-current-row style="width: 100%; margin-bottom: 5px;">
        <el-table-column fixed type="index" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="account" :label="$t('UserView.account')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="nickName" :label="$t('UserView.nickName')" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="name" :label="$t('UserView.name')" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="sex" :label="$t('UserView.sex')" width="100">
          <template v-slot="{ row }">
            <el-tag v-if="row.sex === 0" class="ml-2" type="success">
              {{ $t('pullDown.sex.secret') }}
            </el-tag>
            <el-tag v-else-if="row.sex === 1">
              {{ $t('pullDown.sex.man') }}
            </el-tag>
            <el-tag v-else-if="row.sex === 2" class="ml-2" type="danger">
              {{ $t('pullDown.sex.woman') }}
            </el-tag>
            <el-tag v-else class="ml-2" type="info">
              {{ $t('pullDown.unknown') }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="roleName" :label="$t('UserView.role')" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="regionName" :label="$t('UserView.region')" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="regionNames" :label="$t('UserView.regions')" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column prop="phone" :label="$t('UserView.phone')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="identityCard" :label="$t('UserView.identityCard')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="email" :label="$t('UserView.email')" width="210" show-overflow-tooltip></el-table-column>
        <el-table-column prop="weixin" :label="$t('UserView.weixin')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="qq" :label="$t('UserView.qq')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="photoUrl" :label="$t('UserView.photoUrl')" width="70">
          <template v-slot="{ row }">
            <el-avatar shape="square" :size="40" :src="getUrl(row.photoUrl)" />
          </template>
        </el-table-column>
        <el-table-column prop="birthDate" :label="$t('UserView.birthDate')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="updateUserName" :label="$t('UserView.updateUserName')" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="registerDate" :label="$t('UserView.registerDate')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="updateDate" :label="$t('UserView.updateDate')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="loginDate" :label="$t('UserView.loginDate')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="loginNum" :label="$t('UserView.loginNum')" width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="userStatus" :label="$t('UserView.userStatus')" width="100">
          <template v-slot="{ row }">
            <el-tag v-if="row.userStatus === 1">
              {{ $t('pullDown.userStatus.enable') }}
            </el-tag>
            <el-tag v-else-if="row.userStatus === 2" class="ml-2" type="danger">
              {{ $t('pullDown.userStatus.disable') }}
            </el-tag>
            <el-tag v-else class="ml-2" type="info">
              {{ $t('pullDown.unknown') }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="300">
          <template #default="scope">
            <el-button type="primary" size="small" @click="goOperate(scope.$index, scope.row, 'detail')">
              {{ $t('common.button.detail') }}
            </el-button>
            <el-button type="warning" size="small" @click="goOperate(scope.$index, scope.row, 'edit')" v-show="isButton('user.update')">
              {{ $t('common.button.edit') }}
            </el-button>
            <el-button type="danger" size="small" @click="deleteRow(scope.$index, scope.row)" v-show="isButton('user.delete')">
              {{ $t('common.button.delete') }}
            </el-button>
            <el-button size="small" @click="resetPass(scope.$index, scope.row)" v-show="isButton('user.delete')">
              重置密码
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="modulePagination">
      <el-pagination
          background
          v-model:current-page="pageNum"
          v-model:page-size="pageSize"
          :page-sizes="[10, 30, 50, 75, 100]"
          :small="false"
          layout="prev, pager, next, sizes, jumper, total"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>

    <userAdmin-operate ref="operate" @operateClick="listData"/>
  </div>
</template>

<script>
import {deleteUserAdminById, listUserAdmin, resetPass} from "@/http/basics/userApi";
import {resourceIsUser} from "@/util/resource";
import userAdminOperate from "@/views/basics/user/userAdmin-operate";
import {getRolePullDown} from "@/http/PullDownApi";
import {getNewYmdStr} from "@/util/DateUtil";
import {ElLoading, ElNotification} from "element-plus";

export default {
  name: "styleVeiw",
  components: {userAdminOperate},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      tabHeight: 0, // 表格高度

      selectData: {}, // 查询框数据集合对象
      fromData: {}, // 查询条件
      formLabelWidth: '88px',

      tableData: [],
      total: 0,           // 总条数
      pages: 0,           // 总页数
      pageNum: 1,         //当前页码
      pageSize: 30,        // 每页条数

      url: process.env.VUE_APP_BACKEND_API_URL,
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getSelectData();
    this.listData();
    this.refreshTableHeight();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 刷新表格高度
    refreshTableHeight() {
      // 创建一个 ResizeObserver 实例
      const resizeObserver = new ResizeObserver(entries => {
        // entries 是 ResizeObserverEntry 对象的数组，包含观察到的元素信息
        for (const entry of entries) {
          if (entry.target === this.$refs.tableDiv) {
            // 如果观察到的元素是你要监听的元素
            // console.log('窗口大小改变，新的表格高度：', entry.contentRect.height);
            this.tabHeight = this.$refs.tableDiv.offsetHeight - 20;
          }
        }
      });
      // 将 ResizeObserver 注册到要观察的元素
      resizeObserver.observe(this.$refs.tableDiv);
    },
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },

    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    // 获取查询条件数据
    getSelectData() {
      // 获取角色的数据
      getRolePullDown().then(res => {
        if (res.data.status == 200) this.selectData.roleList = res.data.data;
      }).catch((erro) => { });
    },
    // 列表数据请求
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      if (this.fromData.birthDateListList != null) {
        this.fromData.startDirthDate = this.fromData.birthDateListList[0];
        this.fromData.endDirthDate = this.fromData.birthDateListList[1];
      }

      this.fromData.pageSize = this.pageSize;
      this.fromData.pageNum = this.pageNum;
      listUserAdmin(this.fromData).then(res => {
        this.tableData = res.data.data.list;
        // 给分页组件的数据赋值
        this.total = res.data.data.total;
        this.pages = res.data.data.pages;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
      }).catch((erro) => {
        console.log(erro, 'erro')
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    // 设置每页条数方法 val=条数
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageNum = 1;                       // 修改当前页为第1页
      this.pageSize = val;               // 修改每页条数
      this.listData();
    },
    // 点击 前一页、后一页、页码 事件
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageNum = val;                 // 修改当前页码
      this.listData();
    },
    // 重置按钮
    reset() {
      this.fromData = {};
      this.listData();
    },

    // 新增
    add() {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = "add";
      this.$refs.operate.form.birthDate = getNewYmdStr(new Date())
    },
    // 详情 / 编辑 执行方法
    goOperate(index, row, type) {
      row.urlStr = this.getUrl(row.photoUrl);
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = type;
      this.$refs.operate.getUserById(row.id);
      if (type == "detail") {
        // 开启只读 | 深拷贝创建了目标对象的一个完整副本
        this.$refs.operate.isReadonly = true;
      } else if (type == "edit") {
        this.$refs.operate.rowData = row;
      }
    },
    // 删除
    deleteRow(index, row) {
      this.$confirm(this.$t('common.dialog.deleteTips') , this.$t('common.dialog.deleteTitle'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'
      }).then(() => {
        // 后台请求删除数据库数据
        deleteUserAdminById({ id: row.id}).then(res => {
          if (res.data.status == 200) {
            // 删除前端渲染数组的数据
            this.tableData.splice(index, 1);
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        }).catch(error => { });
      })
    },
    resetPass(index, row) {
      this.$confirm(this.$t('UserView.resetPassTxt'), this.$t('common.dialog.title'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'success'
      }).then(() => {
        // 后台请求删除数据库数据
        resetPass(row).then(res => {
          if (res.data.status == 200) {
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        }).catch(error => { });
      })
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>

</style>
